import React from 'react';
import { Link } from 'react-router-dom';
import '../styles/App.css';

const NotFoundPage = () => (
    <div>
        <h3 className="wedding-not-found">Where are you going? Head back to the <div className="Wedding-inline-link"><Link to="/">site!</Link></div></h3>
    </div>
);

export default NotFoundPage;



