import React from 'react';

const Card = (props) => (
    <div className="Wedding-card">

        { props.detail.name && <div className="Wedding-card-name">{ props.detail.name }{ props.detail.description && ':' }</div> }
        { props.detail.description ?
            <div className="Wedding-card-description">{ props.detail.description }</div> :
            !props.detail.name && <div className="Wedding-card-description">{ props.detail }</div>
        }
        { props.children }

    </div>
);

export default Card;



