import React from 'react';
import Card from './Card';

const CardSection = (props) => (
    <div className={ `${props.className}` }>
        <div className="Wedding-card-section-title">{ props.section.title }</div>
        <div>
            {
                props.section.details.map((detail, index) => {
                    return (
                        <React.Fragment key={ `travel-details-${index}` } >
                            <Card detail={ detail }>
                                {
                                    (props.section.links && props.section.links.length > 0) &&
                                    <div className="Wedding-travel-link-section">
                                        {
                                            props.section.links.map(link => {
                                                return (
                                                    <div className="Wedding-inline-link" key={ link.title }>
                                                        <a href={ `http${link.secure ? link.secure : ''}://${ link.link }` }>{ link.title }</a>
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                                }
                            </Card>
                        </React.Fragment>
                    )
                })
            }
        </div>
    </div>
);

export default CardSection;



