import React from 'react';

const Hero = () => (
    <div className="Wedding-hero">
        <div className="Wedding-text-overlay">
            <div className="Wedding-overlay large">You Are Invited</div>
            <div className="Wedding-overlay small">to the</div>
            <div className="Wedding-overlay large">Young Wedding</div>
        </div>
    </div>
);

export default Hero;



