import React from 'react';
import { NavLink } from 'react-router-dom';

const Header = () => (
    <div className="Wedding-header">
        <NavLink className="Wedding-header-link" to="/" activeClassName="is-active" exact={ true }>Home</NavLink>
        <NavLink className="Wedding-header-link" to="/travel" activeClassName="is-active" exact={ true }>Travel / Lodging</NavLink>
        <NavLink className="Wedding-header-link" to="/party" activeClassName="is-active" exact={ true }>Wedding Party</NavLink>
    </div>
);

// <NavLink to="/" activeClassName="is-active" exact={true}>Dashboard</NavLink>

export default Header;



