import React from 'react';
import '../styles/App.css';
import Hero from './subcomponents/Hero';
import CardSection from './subcomponents/CardSection';
import frontPageDetails from '../fixtures/frontPageDetails';


export class FrontPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = frontPageDetails;
    }
    render() {
        return (
            <div>
                <Hero />
                <div className="wedding-container wedding-details">
                    {
                        this.state.sections.map((section) => {
                            return (
                                <CardSection
                                    className="Wedding-card-section"
                                    key={ section.title }
                                    section={ section }
                                />
                            )
                        })
                    }
                </div>
            </div>
        );
    }
}

export default FrontPage;



