import React from 'react';
import Hero from './subcomponents/Hero';
import travelDetails from '../fixtures/travelDetails';
import CardSection from './subcomponents/CardSection';
import '../styles/App.css';

export const Travel = () => (
    <div>
        <Hero />
        <div className="Wedding-travel-container">
            {
                travelDetails.map(section => {
                    return (
                        <React.Fragment key={ section.title }>
                            <CardSection
                                className="Wedding-travel-section"
                                key={ section.title }
                                section={ section }
                            />
                        </React.Fragment>
                    )
                })
            }
        </div>
    </div>
)

export default Travel;



